
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
// ** Invoice List Sidebar
import withReactContent from 'sweetalert2-react-content'
// import AddQuiz from '../add'
import Swal from 'sweetalert2'
// ** Store & Actions
import { getcricketTotal, clearData, getCricketSquad, getCricketSquadadd, cricketsquadviwe, deletesquad, searchCricketSquad } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, Filter, Trash2 } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Button, Spinner, Input } from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid, getval } from "../../../Report/CricketReport/store/actions"
// ** Styles
import Select from 'react-select'
import Breadcrumbs from '@components/breadcrumbs'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useHistory } from 'react-router-dom'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

// ** Table Header
const CustomHeader = () => {

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
     
    </div>
  )
}

const UsersList = () => {

  const typeOptions = [
    { value: '1', label: 'Batsman' },
    { value: '2', label: 'Bowler' },
    { value: '3', label: 'All Rounder' },
    { value: '4', label: 'Wicket Keeper' },
    { value: '5', label: '' }
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.squad)
  const store1 = useSelector((state) => state.squad)
  const sto = useSelector((state) => state.rep)
  const MySwal = withReactContent(Swal)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const history = useHistory()
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [click, setClick] = useState(false)
  const [error, setError] = useState(false)
  const [searchtype, setSearchType] = useState('')
  const [searchName, setSearchName] = useState('')
  const [page, setPage] = useState(1)
  const [value] = useState(null)  

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })


  const fixture = sto.fixture_name.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = sto.series_name.filter(s => s.series_name === serilist)
  const filterid2 = sto.fixture_name.filter(f => f.fixture_name === fixlist)
  const filterall = sto.fixture_name.map(f => f.fixture_name)

  useEffect(() => {
    dispatch(getcricketTotal(page))
  }, [page])

  useEffect(() => {
    dispatch(getAllFixture(serilist))
    console.log(sid)
  }, [serilist])

  const idea = `{"series_id":${serilist}, "fixture_id":"${click ? fixlist : ""}", "page":${1}, "player_name":"${searchName ? searchName : ""}", "player_type":"${searchtype ? searchtype : ""}" }`
  const submit = (e) => {
    e.preventDefault()
    setVal(true)
    console.log(filterall.map(f => f === fixlist))
    setCurrentPage(1)
    //  const idea = `{"series_id":${serilist}, "fixture_id":"${fixlist}", "page":${currentPage}}`
    console.log(fixlist)
    if (serilist === "") {
      setError(true)
    } else {
      dispatch(getCricketSquadadd(idea))
    }
    dispatch(collectseriesid(serilist))
    dispatch(collectfixtureid(fixlist))
  }

  useEffect(() => {
    if (store?.cricket_data_add.length !== 0) {
      setListData(store?.cricket_data_add)
    } else {
      if (store?.cricket_data_add.length === 0 && val) {
        setListData(store?.cricket_data_add)
      } else {
        setListData(store?.cricket_data)
      }
    }
  }, [submit])

  const serieslist = (e) => {
    setVal(false)
    console.log(error)
    setError(false)
    setSerilist(e?.value)
    setRefresh(true)
    dispatch(getval(false))
    // dispatch(getAllFixture(filterid[0]?.id))
    setSid(filterid[0]?.id)
    console.log(filterid[0]?.id)
    setClick(false)
    // return store.series_name.filter(s => s.series_name === list)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setClick(true)
    dispatch(getval(true))
    setRefresh(false)
    console.log(e.map(e => e.value))
    setFid(filterid2[0]?.id)
    console.log(filterid2[0]?.id)
    console.log(fid)
    // return store.series_name.filter(s => s.series_name === list)
  }

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(getCricketSquadadd(idea))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(fixlist))
    }
  }
console.log()
  // ** Get data on mount
  const idea1 = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.getval ? sto.fixtureid : ""}", "page":${currentPage}, "player_name":"${searchName ? searchName : ""}", "player_type":"${searchtype ? searchtype : ""}" }`
  useEffect(() => {
    // const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.fixtureid}", "page":${currentPage}}`
    if (store.cricket_data_add.length !== 0) {
      dispatch(getCricketSquadadd(idea1)) 
    } else {
      dispatch(getCricketSquad(currentPage))
    }
    dispatch(getAllSeries())
  }, [paginate])

  useEffect(() => {
    if (store.isloading) {
      <Spinner />
      console.log("click")
      console.log(store.isloading)
    } else {
      setListData(store.cricket_data)
      console.log(store.isloading)
      console.log(store)
    }
  }, [store.isloading])

  const deleteHandler = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        dispatch(deletesquad(id))
        console.log(id)       
      }
    })
  }

  const editHandler = (row) => {
    history.push({ pathname: "/cricketsquad/edit" })
    dispatch(cricketsquadviwe(row.id))
  }


  //const columnsList = useCallback(columns(), [])
  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.cricket_data.filter((userItem) => {

        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_data)
    }
  }, [searchTerm])

  useEffect(() => {
    console.log('Mounted')
    return () => {
      console.log('Unmounted')
      dispatch(clearData())
    }
  }, [])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  console.log(store)
  console.log(store.cricket_data_add)
  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.cricket_data_add?.data?.length > 0 ?  store.cricket_data_add?.last_page : store.searchCricketSquad?.data?.length > 0 ? store.searchCricketSquad?.last_page : store.cricket_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }
console.log(store.searchCricketSquad?.data?.length)
  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store.cricket_data_add?.data?.length >= 0) {
      return store.cricket_data_add?.data
    } else if (store.searchCricketSquad?.data?.length) {
      return store.searchCricketSquad?.data
    } else if (listData.length === 0 && isFiltered) {
      return []
    }
  }

  useEffect(() => {
    dispatch(searchCricketSquad(currentPage, searchName, searchtype))
  }, [currentPage])

  console.log(searchtype)

  
  const typeHandler = (value) => {
    setSearchType(value)
  }

  const columns = [
    {
      name: '#',
      maxWidth: '10px',
      sortable: true,
      cell: (row, pos) => `#${store.searchCricketSquad?.from + pos}`
    },
   
    {
      name: <span style={{marginLeft:'50px'}}>Team Name</span>,
      minWidth: '260px',
      selector:'fixture_sh',
      sortable: true,
      cell: (row) => ( 
        
          <Row>
            <Col>
            <img src={row.path} style={{ height: '40px', width: '40px' }}/>&nbsp;&nbsp;&nbsp;&nbsp;{row.team_name}
            </Col>
            
            </Row>
    
      )
    
    },
 
    {
      name: 'Player Name',
      maxWidth: '100px',
      minWidth: '200px',
      selector: 'player_name',
      sortable: true,
      cell: (row) => row.player_name
    },
    {
      name: 'player Type',
      maxWidth: '100px',
      minWidth: '200px',
      selector: 'player_type',
      sortable: true,
      cell: (row) => row.player_type
    },
    {
      name: 'Credits',
      maxWidth: '100px', 
      minWidth: '200px',
      selector: 'player_value',
      sortable: true,
       // <td className='text-nowrap'> ODI {row.player_value[2]}</td> 
      // <td className='text-nowrap'>T20 {row.player_value[4]}</td>
      //cell: (row) => row.player_value.split(',')
     // cell: (row) => `TEST ${row.player_value[0]}, ODI ${row.player_value[2]}, T20 ${row.player_value[4]}`
      cell: (row) => {
        const cj = row.player_value.split(',')
        return (
          
          <tbody>
            <tr >
            <td  className='text-nowrap  my-2'><span style={{color:'green'}}>TEST </span>{cj[0]}</td> 
           </tr> 
          
           <tr>
           <td  className='text-nowrap'> <span style={{color:'blue'}}>ODI&nbsp;</span>  {cj[1]}</td>
           </tr>
           <tr>
           <td  className='text-nowrap'> <span  style={{color:'#FF33E6'}}>T20&nbsp;&nbsp;</span>  {cj[2]}</td>
           </tr>
          </tbody>
          
        
        )
      }
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit" onClick={() => editHandler(row)} />
          {/* </a> */}
          <Trash2 width="17" className="ml-2 cursor-pointer" title="Delete" onClick={() => deleteHandler(row.id)} />
        </div>
      )
    }
  ]

   // ************************************************************************************* //

   const options = []
   for (let i = 0; i < store1.cricket_total?.data?.total; i++) {
     if (page === store1.cricket_total?.data?.current_page) {
       options.push({
         value: `${store1.cricket_total?.data?.data[i]?.id}`,
         label: `${store1.cricket_total?.data?.data[i]?.series_name}`
       })
     }
     
   }
   const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
   const optionsPerPage = 10
   
   const loadOptions = async (search, prevOptions, page) => {
     await sleep(1000)
     if (page === store1.cricket_total?.data?.current_page) {
       const nextData = store1.cricket_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
       const scrollLength = [...prevOptions, ...nextData]
       let filteredOptions
       if (!search) {
         filteredOptions = scrollLength.length > 10 ? scrollLength : options
       } else {
         const searchLower = search.toLowerCase()
         filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
         )
       }
   
       const hasMore = Math.ceil(store1.cricket_total?.data?.total / optionsPerPage) > page
       const slicedOptions = filteredOptions.slice(
         (page - 1) * optionsPerPage,
         page * optionsPerPage
       )
   
       return {
         options: slicedOptions,
         hasMore
       }
     }
   }
   const defaultAdditional = {
     page: 1
   }
   
   const loadPageOptions = async (q, prevOptions, { page }) => {
     setPage(page)
     const { options, hasMore } = await loadOptions(q, prevOptions, page)
     return {
       options:[...options],
       hasMore,
   
       additional: {
         page: page + 1
       }
     }
   }
   
    // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter...</div>,
      content: (
        <>
          <Row>
          <Col className='mb-1' md='6' sm='12'>
            <Label>series</Label>
              <AsyncPaginate
              menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
              {serilist === "" && val ? <h5 className='text-danger'>Please enter a value</h5> : ""}
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Fixture</Label>
              <Select
                isDisabled={store.fixloader}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                options={fixture ? fixture : ""}
                placeholder='Select cricket fixture...'
                isClearable={true}
              />
            </Col>
          </Row>         
          <Row>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Name:</Label>
              <Input
                id="search-invoice"
                className="w-100"
                type="text"
                value={searchName}
                placeholder='Enter player name'
                onChange={(e) => setSearchName(e.target.value)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => typeHandler(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={typeOptions}
                placeholder='search player type...'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >submit</Button.Ripple>
        </>
      )
    }
  ]

  return (
    <Fragment>
      <Breadcrumbs breadCrumbTitle='Cricket Squad' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              cricket_data={store.cricket_data}
              cricket_data_add={store.cricket_data_add}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList
